import React from 'react'
import { useDispatch } from 'react-redux'
import { viewDate, initializeApod } from '../reducers/apodReducer'

import { useField } from '../hooks/index'

const PastApodsForm = () => {
  const dispatch = useDispatch()

  const initApod = () => {
    dispatch(initializeApod())
  }

  const searchDate = (event) => {
    event.preventDefault()
    const dateString = `${year.value}-${month.value}-${day.value}`
    console.log(dateString)

    dispatch(viewDate(dateString))
  }

  const currentYear = new Date().getFullYear
  const year = useField('text', currentYear)
  const month = useField('text', 12)
  const day = useField('text', 31)


  return (
    <div className='field-box'>
      <b>View previous days&apos; pictures</b>
      <br />
      <form onSubmit={searchDate}>
        <input {...year} size='6' placeholder='YYYY' className='apod-search-field' />
        <input {...month} size='3' placeholder='MM' className='apod-search-field' />
        <input {...day} size='3' placeholder='DD' className='apod-search-field' />
        <br />
        <button type='submit' className='submit-button'>Search</button> &nbsp; &nbsp;
      </form><button onClick={initApod} className='submit-button'>Today&apos;s picture</button>
    </div>

  )
}


export default PastApodsForm