import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { setNotif, resetNotif } from '../reducers/notifReducer'

export const useField = (type, maxVal) => {
  const dispatch = useDispatch()
  const changeNotif = (text, notifType) => {
    dispatch(setNotif(text, notifType))
  }

  const removeNotif = () => {
    dispatch(resetNotif())
  }
  const [value, setValue] = useState('')

  const onChange = (event) => {
    const valueString = event.target.value
    if (!Number(valueString + 1) && valueString !== '') {
      changeNotif('Give value as a number.')
    } else if (Number(valueString) > maxVal) {
      changeNotif('Not a valid input.')
    } else {
      (removeNotif())
    }
    setValue(valueString)
  }

  const onReset = () => {
    setValue('')
    return
  }

  return {
    type,
    value,
    onChange,
    onReset
  }
}

export const useLoginField = (type) => {
  
  const [value, setValue] = useState('')

  const onChange = (event) => {
    const valueString = event.target.value
    setValue(valueString)
  }

  const onReset = () => {
    setValue('')
    return
  }

  return {
    type,
    value,
    onChange,
    onReset
  }
}
