import React from 'react'
import Togglable from './Togglable'
import { useSelector } from 'react-redux'
import Notification from './Notification'
import PastApodsForm from './PastApodsForm'
import ReactPlayer from 'react-player'

// eslint-disable-next-line react/prop-types
const ImageDesc = ({ imageDesc }) => {
  return (
    <div className='apod-desc'>
      {imageDesc}
    </div>
  )
}

const Apod = () => {
  const apod = useSelector(state => state.apod)

  if (apod === undefined) {
    console.log(apod)
    return null
  }

  const copyright = (apod.copyright === undefined) ? 'Copyright: Public domain' : `(c) ${apod.copyright}`
  //console.log('apod:', apod)
  console.log('mediatype', apod.media_type)

  if (apod !== undefined && apod.media_type === "image") {
    return (
      <div className='content'>
        <h2>Astronomy Picture of the Day</h2>
        <h3>{apod.date}</h3>
        <div key={apod.url}>
          <div className='apodImg'>

            <img src={apod.url} alt={apod.title} />
            <br />
            <div className="apodCopyright">

              <b>{copyright} </b>
            </div>
            <br />
            <br />
          </div>
          <Togglable toggLabel='See description'>
            <b>Description:</b>
            <ImageDesc imageDesc={apod.explanation} />
          </Togglable> </div>
        <div className='previous-apod'>
          <PastApodsForm />
          <div className='notif-div'>
            <Notification />
          </div>
        </div>
        <div className='external-link'>
          All material (c) Nasa.
          <br />
          <a href='https://apod.nasa.gov/apod/' target="_blank" rel="noopener noreferrer">APOD on Nasa homepage</a></div>
        <div>
        </div>
      </div>
    )
  }
  else {
    return (
      <div className='content'>
        <h2>Astronomy Picture of the Day</h2>
        <h3>{apod.date}</h3>
        <div key={apod.url}>
          <div className="apodImg">
            <div className='apodVid'>
              <ReactPlayer url={apod.url} />
            </div>
            <div className="apodCopyright">
              <br />
              <b>{copyright} </b>
              <br />
              <br />
            </div>
          </div>
          <Togglable toggLabel='See description'>
            <b>Description:</b>
            <ImageDesc imageDesc={apod.explanation} />
          </Togglable>
        </div>
        <div className='previous-apod'>
          <PastApodsForm />
          <div className='notif-div'>
            <Notification />
          </div>
        </div>
        <div className='external-link'>
          All material (c) Nasa.
          <br />
          <a href='https://apod.nasa.gov/apod/' target="_blank" rel="noopener noreferrer">APOD on Nasa homepage</a></div>
      </div>
    )
  }
}


export default Apod