import marsweatherService from '../services/marsweather'


const initialState = {
  all_data: {
    AT: { av: null },
    HWS: { av: null },
    PRW: { av: null }
  },
  today: null
}

export const initializeMars = () => {
  console.log('initializeMars called')
  return async dispatch => {
    const mars = await marsweatherService.get()
    const today = await marsweatherService.getToday()
    //const daysData = mars
    const daysData = mars[`${today}`]
    const toDispatch = {
      type: 'INIT_MARS',
      data: {
        day: today,
        days_data: daysData,
        today: today
      }
    }
    //console.log(toDispatch)
    dispatch(toDispatch)
  }
}

export const viewDay = (day) => {
  return async dispatch => {
    console.log(`${day} called`)
    const mars = await marsweatherService.get()
    const today = await marsweatherService.getToday()
    const daysData = mars[`${day}`]
    const toDispatch = {
      type: 'ONE_DAY',
      data: {
        day: day,
        days_data: daysData,
        today: today
      }
    }
    dispatch(toDispatch)
  }
}

const marsReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'INIT_MARS':
      return action.data
    case 'ONE_DAY':
      return action.data
    default:
      return state
  }
}

export default marsReducer