import React from 'react'
import { useSelector } from 'react-redux'


const Notification = () => {

  const text = useSelector(state => state.notif.text)
  const notifType = useSelector(state => state.notif.notifType)

  //console.log(notifType)
  if (notifType === null || notifType === undefined) {
    return (
      <div className='empty-notif'>
        &nbsp;
      </div>
    )

  }
  return (
    <div className={`${notifType} notification`}>
      <div className='exclamation'>!</div>
      {text}
    </div>

  )

}


export default Notification