import searchService from '../services/search'

const initialState = {
  res: [],
  img: null,
  desc: null,
  page: 1,
  keyword: null,
  errored: false
}

export const imgError = () => {
  console.log('errored')
  return async dispatch => {
    dispatch({
      type: 'SEARCH_ERROR',
    })
  }
}

export const performSearch = (keyword) => {
  return async dispatch => {
    const res = await searchService.searchImg(keyword)
    dispatch({
      type: 'SEARCH_RESULTS',
      data: {
        res,
        img: null,
        desc: null,
        page: 1,
        keyword,
        errored: false
      }
    })
  }
}

export const performNextPageSearch = (keyword, page) => {
  return async dispatch => {
    const res = await searchService.nextPage(keyword, page)
    dispatch({
      type: 'SEARCH_RESULTS',
      data: {
        res,
        img: null,
        desc: null,
        page,
        keyword,
        errored: false
      }
    })
  }
}

export const viewImage = (key, alt, desc) => {
  return async dispatch => {
    const imgArray = await searchService.getImg(key)
    console.log('imgArray: ', imgArray)
    const img = imgArray[0]
    dispatch({
      type: 'SET_IMG',
      data: {
        img,
        alt,
        desc: desc,
        errored: false
      }
    })
  }
}

export const initSearch = () => {
  return async dispatch => {
    dispatch({
      type: 'INIT_SEARCH',
      data: {
        res: [],
        img: null,
        desc: null,
        errored: false
      }
    })
  }
}

const searchReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SEARCH_RESULTS':
      return {
        ...state,
        res: action.data.res,
        desc: action.data.desc,
        page: action.data.page,
        keyword: action.data.keyword,
        errored: false
      }
    case 'INIT_SEARCH':
      return action.data
    case 'SEARCH_ERROR':
      return {
        ...state,
        errored: true,
      }
    case 'SET_IMG':
      return {
        ...state,
        img: action.data.img,
        alt: action.data.alt,
        desc: action.data.desc,
        errored: action.data.errored
      }
    default:
      return state
  }


}


export default searchReducer