import React from 'react'
import { performSearch } from '../reducers/searchReducer'
import { useDispatch } from 'react-redux'
import { useField } from '../hooks/index'

const SearchForm = () => {

  const dispatch = useDispatch()

  //const [newContent, setNewContent] = useState('')
  //const dispatch = useDispatch()

  const keywords = useField('text')

  const doSearch = (event) => {
    event.preventDefault()
    dispatch(performSearch(keywords.value))
  }


  return (
    <div>
      <div className='field-box'>
        <h2>Search for images</h2>
        <p>Search for media in the<br /><a href='https://images.nasa.gov/' target="_blank" rel="noopener noreferrer">Nasa Image And Video Library</a>.</p>
        <form onSubmit={doSearch}>
          <input {...keywords} size='95%' placeholder='keywords' className='apod-search-field' />
          <br />
          <button type="submit" className='submit-button'>Search</button>
        </form>
      </div>
    </div>)
}

export default SearchForm