import React, { useEffect } from 'react'
import Footer from './components/Footer'
import Apod from './components/Apod'
import Comments from './components/Comments'
import MarsWeather from './components/MarsWeather'
import Search from './components/Search'
import { useDispatch } from 'react-redux'
import { initializeApod } from './reducers/apodReducer'
import { initializeMars } from './reducers/marsReducer'
import { initializeNotif } from './reducers/notifReducer'
import {
  Switch, Route, Link
} from 'react-router-dom'
import './App.css'

const padding = {
  padding: '15px'
}

const Menu = () => {

  return (
    <div className='menu'>
      <h1>SpaceApp</h1>
      <Link style={padding} to="/">APOD</Link>
      <Link style={padding} to="/mars">Mars Weather</Link>
      <Link stype={padding} to="/search">Image Search</Link>
      <Link style={padding} to="/comments">Comments</Link>
    </div>
  )
}

const App = () => {

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(initializeApod())
  }, [dispatch])

  useEffect(() => {
    dispatch(initializeMars())
  }, [dispatch])

  useEffect(() => {
    dispatch(initializeNotif())

  })


  return (
    <div className='app'>
      <Menu />
      <br />
      <div className='content-container'>
        <Switch>
          <Route path="/comments">
            <Comments />
          </Route>
          <Route path="/mars">
            <MarsWeather />
          </Route>
          <Route path="/search">
            <Search />
          </Route>
          <Route path="/">
            <Apod />
          </Route>
        </Switch>
      </div>
      <Footer />
    </div>
  )
}

export default App
