import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { viewImage, performNextPageSearch } from '../reducers/searchReducer'
import SearchResult from './SearchResult'

const SearchResults = () => {
  const resultClassNames = ['result-dark', 'result-bright']

  const dispatch = useDispatch()

  const search = useSelector(state => state.search)
  const searchResult = search.res

  const nextPage = () => {
    const page = search.page
    //console.log(page)
    dispatch(performNextPageSearch(search.keyword, page + 1))
  }

  const prevPage = () => {
    const page = search.page
    //console.log(page)
    dispatch(performNextPageSearch(search.keyword, page - 1))
  }

  const findImg = async (key, alt, desc) => {
    dispatch(viewImage(key, alt, desc))
    if (document.getElementById('apodImg') !== null) {
      document.getElementById('apodImg').scrollIntoView({ behavior: 'smooth' })
    }
  }
  if (searchResult.length === 0) {
    return (
      <div>No results!</div>
    )
  } else if (searchResult.length === 100) {
    return (<div>
      <h2>Search results</h2>
      <div>Your search returned <b>over 100</b> results.</div>
      <br />
      {searchResult.map((res, index) =>
        <SearchResult result={res} key={res.data[0].nasa_id} styleName={resultClassNames[index % 2]} findImg={findImg} />
      )}
      <div className='next-and-previous'>
        {search.page > 1 && <div className='result-page-nav' onClick={prevPage}>
          <b>previous page</b>
        </div>}
        <div className='result-page-nav' onClick={nextPage}>
          <b>next page</b>
        </div>
      </div>
    </div>)
  } else {
    return (
      <div>
        <h2>Search results</h2>
        {search.page > 1 && <div>viewing page {search.page} of <b>over 100</b> results</div>}{search.page === 1 && <div>Your search returned <b>{searchResult.length}</b> results.</div>}
        <br />
        {searchResult.map((res, index) =>
          <SearchResult result={res} key={res.data[0].nasa_id} styleName={resultClassNames[index % 2]} findImg={findImg} />

        )}
        <div className='next-and-previous'>

          {search.page > 1 && <div className='result-page-nav' onClick={prevPage}>
            <b>previous page</b>
          </div>}
        </div>
      </div>)
  }
}

export default SearchResults