import React from 'react'

const LoginForm = ({
  username,
  password,
  handleUsernameChange,
  handlePasswordChange,
  handleSubmit

}) => {

  return (
    <div>
      <h2>
        Login to leave a comment
      </h2>
      <form onSubmit={handleSubmit} className="commentForm">
        <div>
          Username:
        <br />
          <input id='username'
            value={username}
            onChange={handleUsernameChange}
            className="apodSearchField"
          />
        </div>
        <div>
          Password:
          <br />
          <input id='password'
            type="password"
            value={password}
            onChange={handlePasswordChange}
            className="apodSearchField"
          />
        </div>
        <button id='login-button' type="submit" className="submit-button">login</button>
      </form>
    </div>
  )
}

export default LoginForm
