import React, { useState, useEffect, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import commentService from '../services/comments'
import CommentForm from './CommentForm'
import LoginForm from './LoginForm'
import SignupForm from './SignupForm'
import loginService from '../services/login'
import userService from '../services/users'
import Notification from './Notification'
import Togglable from './Togglable'
import Comment from './Comment'
import { setNotif, resetNotif } from '../reducers/notifReducer'
import { initializeComments, removeComment } from '../reducers/commentsReducer'

const Comments = () => {
  const comments = useSelector(state => state.comments)
  const dispatch = useDispatch()
  const changeNotif = (text, notifType) => {
    console.log('changeNotif called with params', text, notifType)
    dispatch(setNotif(text, notifType))
  }
  const signupToggRef = useRef()

  const removeNotif = () => {
    dispatch(resetNotif())
  }

  useEffect(() => {
    dispatch(initializeComments())
  }, [dispatch])

  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [userId, setUserId] = useState(null)
  const [newUsername, setNewUsername] = useState('')
  const [newName, setNewName] = useState('')
  const [newPassword, setNewPassword] = useState('')

  const handleLogin = async (event) => {
    event.preventDefault()
    try {
      console.log('dispatching login from App for', username)
      const user = await loginService.login({
        username, password,
      })
      console.log(user)
      setUserId(user.id)
      setUsername('')
      setPassword('')
      commentService.setToken(user.token)
      changeNotif(`Logged in as ${username}`, 'notifGreen')
    } catch (exception) {
      changeNotif('Error logging in', 'notifRed')
      setTimeout(() => {
        removeNotif()
      }, 5000)
    }
  }

  const deleteComment = (id) => {
    dispatch(removeComment(id))
  }

  const handleSignup = async (event) => {
    event.preventDefault()
    try {
      console.log('registering', newUsername)
      await userService.post({
        username: newUsername,
        name: newName,
        password: newPassword,
      })
      //console.log(user)
      setNewName('')
      setNewUsername('')
      setNewPassword('')
      signupToggRef.current.toggleVisibility()
      changeNotif(`New user registered: ${newUsername}`, 'notifGreen')
    } catch (exception) {
      changeNotif(`Error signing up: ${exception}`, 'notifRed')
      setTimeout(() => {
        removeNotif()
      }, 5000)
    }
  }


  const handleLogout = async () => {
    console.log("Logout!")
    window.localStorage.removeItem('loggedUser')
    setUserId(null)
    changeNotif("Logged out.", "notifGreen")
  }

  const createComm = {

  }

  // This returns something only if there are comments.
  // Need to separate comments into a different component so that login / sign-up info are displayed regardless.
  return (
    <div className='content'>
      <div className="loginSignup">
        {userId === null &&
          <Togglable toggLabel='Login'>
            <LoginForm
              username={username}
              password={password}
              handleUsernameChange={({ target }) => setUsername(target.value)}
              handlePasswordChange={({ target }) => setPassword(target.value)}
              handleSubmit={handleLogin}
            />
          </Togglable>}

        {userId === null &&
          <Togglable toggLabel='Sign up' ref={signupToggRef}>
            <SignupForm
              username={newUsername}
              password={newPassword}
              handleUsernameChange={({ target }) => setNewUsername(target.value)}
              handlePasswordChange={({ target }) => setNewPassword(target.value)}
              handleNameChange={({ target }) => setNewName(target.value)}
              handleSubmit={handleSignup}
            />
          </Togglable>}

      </div>
      {userId !== null &&
        <button id='logout-button' type="logout" onClick={handleLogout} className="submitButton">logout</button>
      }
      <div className='notif-div'>
        <Notification />
      </div>
      <div className="commentsContainer">
        <h2>
          Comments on this app
        </h2>
        {comments.map(comm =>
          <Comment comment={comm} key={comm.id} userId={userId} deleteComment={deleteComment} />
        )}
        {userId !== null && <CommentForm createComm = {createComm}/>}
      </div>
    </div>

  )

}

export default Comments