var timeoutId = null

const initialState = {
  text: '',
  notifType: null
}

export const setNotif = (text, notifType) => {
  // console.log(timeoutId)
  return async dispatch => {
    const timer = () => setTimeout(() => {
      dispatch(resetNotif())
    }, 5000)
    timeoutId = timer()
    //console.log('timeoutId', timeoutId)
    dispatch({
      type: 'SET_NOTIF',
      data: {
        text: text,
        notifType: notifType
      }
    })
  }
}

export const resetNotif = () => {
  //console.log('reset!')
  return async dispatch => {
    dispatch({
      type: 'RESET',
      notifType: null
    })
  }
}

export const initializeNotif = () => {
  return async dispatch => {
    dispatch({
      type: 'RESET',
      notifType: null
    })
  }

}


const notifReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_NOTIF':
      //console.log(timeoutId)
      //timeoutId && console.log('clearing')
      return action.data
    case 'RESET':
      return {
        text: null,
        notifType: null
      }
    default:
      return state
  }
}


export default notifReducer