import React from 'react'

const LoginForm = ({
  username,
  password,
  name,
  handleUsernameChange,
  handlePasswordChange,
  handleNameChange,
  handleSubmit

}) => {
  //const username = useLoginField('username')
  //const password = useLoginField('password')

  return (
    <div>
      <h2>
        Sign up
      </h2>
      <form onSubmit={handleSubmit} className="commentForm">
        <div>
          Username:
        <br />
          <input id='signup-username'
            value={username}
            onChange={handleUsernameChange}
            className="apodSearchField"
          />
        </div>
        <div>
          Name:
          <br />
          <input id='signup-name'
            type="name"
            value={name}
            onChange={handleNameChange}
            className="apodSearchField"
          />
        </div>
        <div>
          Password:
          <br />
          <input id='signup-pass'
            type="password"
            autoComplete="new-password"
            value={password}
            onChange={handlePasswordChange}
            className="apodSearchField"
          />
        </div>
        <button id='signup-button' type="submit" className="submit-button">sign up</button>
      </form>
    </div>
  )
}

export default LoginForm
