import apodService from '../services/apod'
import { setNotif } from './notifReducer'

const initialState = {
  url: '',
  title: '',
  explanation: ''
}

export const initializeApod = () => {
  console.log('initializeApod called')
  return async dispatch => {
    const apod = await apodService.get()
    const toDispatch = {
      type: 'INIT_APOD',
      data: apod,
    }
    //console.log(toDispatch)
    dispatch(toDispatch)
  }
}

export const viewDate = (date) => {
  console.log(`viewing date ${date}`)
  return async dispatch => {
    const apod = await apodService.getFromDay(date)
    console.log(apod)
    if (apod.status === 400) {
      dispatch(setNotif(apod.data.msg, "notifRed"))
    } else {
      const toDispatch = {
        type: 'VIEW_DATE',
        data: apod
      }
      dispatch(toDispatch)
    }
  }
}

export const resetNotif = () => {
  return { type: 'NOLLAA' }
}

const apodReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'INIT_APOD':
      return action.data
    case 'VIEW_DATE':
      return action.data
    default:
      return state
  }
}

export default apodReducer