import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { viewDay, initializeMars } from '../reducers/marsReducer'
// eslint-disable-next-line react/prop-types

const MarsDayButton = ({ handleView, day }) => {
  return (
    <div className='small-button' onClick={() => handleView(day)}>{day}
    </div>
  )
}

const MarsWeather = () => {

  const dispatch = useDispatch()

  const viewOneDay = (day) => {
    dispatch(viewDay(day))
  }

  const viewAll = () => {
    dispatch(initializeMars())
  }

  //console.log('we\'re in marsweather')
  const mars = useSelector(state => state.mars)

  const todayOnMars = mars.today
  const daySelected = mars.day

  //const todaysData = mars.all_data[`${todayOnMars}`]
  const daysData = mars.days_data
  console.log('today\'s data:', daysData)

  console.log(mars)
  //console.log(mars.all_data[457])

  if (daysData === undefined) {
    return (
      <div className='content'>
        <h2>Data unavailable!</h2>
        Sorry.
        <br /><br />
        <div className='small-button' onClick={viewAll}>Back</div>
      </div>)
  } else {
    //console.log('day\'s data:', daysData)
    //console.log('at: ', daysData.AT)
    //console.log('at.av: ', daysData.AT.av)
    //console.log('pre: ', daysData.PRE)
    const temp = (daysData.AT === undefined) ? '(unavailable)' : daysData.AT.av
    //console.log('temp', temp)
    //console.log('temp.av', temp.av)
    const wind = (daysData.HWS === undefined) ? '(unavailable)' : daysData.HWS.av
    const season = daysData.Season
    const pressure = (daysData.PRE === undefined) ? '(unavailable)' : daysData.PRE.av
    const pressurePercentage = (typeof (pressure) === 'number') ? (pressure * 100 / 10325).toFixed(2) : '(unavailable)'
    if (daySelected === todayOnMars) {
      return (
        <div className='content'>
          <h2>Today&rsquo;s Weather on Mars</h2>
        Today is Sol {todayOnMars}. It&rsquo;s {season}.
          <br /><br />
        Average temperature is {temp} °C
          <br /><br />
        Average wind speed is {wind} m/s
          <br /><br />
        Atmospheric pressure is {pressure} pascals; this is {pressurePercentage} % of Earth&rsquo;s air pressure at sea level.
          <br /><br />
          <blockquote>
            <b>View previous days:</b>
            <br /><br />
            <MarsDayButton day={todayOnMars - 1} handleView={viewOneDay} />
            <MarsDayButton day={todayOnMars - 2} handleView={viewOneDay} />
            <MarsDayButton day={todayOnMars - 3} handleView={viewOneDay} />
            <MarsDayButton day={todayOnMars - 4} handleView={viewOneDay} />
            <MarsDayButton day={todayOnMars - 5} handleView={viewOneDay} />
          </blockquote>
          <div className='external-link'>
          All material (c) Nasa.
          <br />
          <a href='https://mars.nasa.gov/insight/weather/' target="_blank" rel="noopener noreferrer">Insight Weather on Nasa homepage</a>.
          <br />
          Unfortunately, the Insight API is not currently transmitting new weather data. I'm leaving this page up for demo work purposes.</div>
        </div>
      )
    }
    else {
      // const temp = daysData.AT
      // const wind = daysData.HWS
      // const season = daysData.Season
      // const pressure = daysData.PRE
      // const avPressure = pressure.av
      return (
        <div className='content'>
          <h2>Weather on Mars for Sol {daySelected}</h2>
        On Sol {daySelected} it was {season}.
          <br /><br />
        Average temperature was {temp} °C
          <br /><br />
          Average wind speed was {wind} m/s
          <br /><br />
        Atmospheric pressure was {pressure} pascals; this is {(pressure * 100 / 10325).toFixed(2)} % of Earth&rsquo;s air pressure at sea level.
          <br /><br />
          <div className='small-button' onClick={viewAll}>Back</div></div>)

    }
  }
}


export default MarsWeather