import axios from 'axios'
const baseUrl = 'https://images-api.nasa.gov/search'
const collectionUrl = 'https://images-assets.nasa.gov/image'
const metadataUrl = 'https://images-api.nasa.gov/metadata'

const searchImg = async (keyword) => {
  const res = await axios.get(`${baseUrl}?q=${keyword}&media_type=image&page=1`)
  //console.log(res)
  const resultArray = res.data.collection.items.filter(result => result.data)
  return resultArray
}

const nextPage = async (keyword, page) => {
  const res = await axios.get(`${baseUrl}?q=${keyword}&media_type=image&page=${page}`)
  const resultArray = res.data.collection.items.filter(result => result.data)
  return resultArray
}

const getImg = async (id) => {
  const res = await axios.get(`${collectionUrl}/${id}/collection.json`)
  //console.log('headers: ', res.headers)
  return res.data
}


//turha??
const getMetadata = async (id) => {
  const res = await axios.get(`${metadataUrl}/${id}`)
  return res
}


export default {
  searchImg,
  getImg,
  getMetadata,
  nextPage
}