import commentService from '../services/comments'


const commentReducer = (state = [], action) => {
  //console.log('reducing!')
  switch (action.type) {
    case 'NEW_COMM':
      return [...state, action.data]
    case 'REMOVE_COMM':
      return action.data
    default:
      return state
    case 'ONE_BLOG':
      return action.data
    case 'INIT_COMMS':
      return action.data
  }
}

export const removeComment = (comm) => {
  const id = comm.id
  return async dispatch => {
    console.log(comm)
    await commentService.remove(id)
    const comments = await commentService.getAll()
    dispatch({
      type: 'INIT_COMMS',
      data: comments
    })
  }
}


export const createComment = (comment) => {
  return async dispatch => {
    const newComm = await commentService.create(comment)
    dispatch({
      type: 'NEW_COMM',
      data: newComm,
    })
  }
}

export const initializeComments = () => {
  return async dispatch => {
    const comments = await commentService.getAll()
    const toDispatch = {
      type: 'INIT_COMMS',
      data: comments,
    }
    console.log(toDispatch)
    dispatch(toDispatch)
  }
}


export default commentReducer