import React from 'react'
import commentService from '../services/comments'
import comments from '../services/comments'

const Comment = ({ comm, userId, deleteComment }) => {

  const handleRemove = async () => {
    console.log('removing!')

    deleteComment(comm.id)
  }

  //console.log(comm)
  if (comm.user !== undefined) {

    return (
      <div className="comment">
        &quot;{comm.content}&quot;<br />
        <blockquote>
          by <b>{comm.user.name}</b>
        </blockquote>
        {userId === comm.use &&
          <button className="submit-button" onClick={handleRemove}>Remove comment</button>
        }
      </div>)
  }
  return (
    <div >
      &quot;{comm.content}&quot;<br />
      <blockquote>
        by <b>{comm.user}</b>
      </blockquote>
    </div>
  )
}

export default Comment